<template>
    <div id="outermain">
    	<div class="container">
            <div class="row">
            
                <section id="maincontent" class="ten columns positionleft">
                	<div class="padcontent">
                
                    	<section class="content" id="login">
                        
                            <div class="breadcrumb"> Home / <a>登录</a></div>
                            <h1 class="pagetitle">登录</h1>
                            
                            <div id="login-process">
                                <span class="first">&nbsp;</span>
                                <span class="old">注册</span>
                                <span class="active">登录</span>
                                <span> 选择</span>
                                <span> 领用</span>
                                <span class="last"> 退还</span>
                                <div class="clear"></div>
                            </div>

                            <div class="one_half columns" style="text-align:center">
                                <h3>登录</h3>
                                <form method="post" action="" id="loginform">

                                <fieldset>
                                <label style="margin-left: -110px;">帐号：</label><br />
                                <input type="text"  v-model='username' style="margin-left: 160px;borderColor:'black';border:1px solid gray;width:300px" /><br />=
                                <label style="margin-left: -110px;">密码：</label><br />
                                <input type="password" class="text-input" v-model='password' style="width:300px;borderColor:'black';border:1px solid gray;float:right;margin-left:160px" /><br/>

                                </fieldset>
                                  <input style="margin-left: 15px;" type="button" class="button" @click='login()' value="登录" />
                                </form><br>
                                <a style="margin-left: 15px;">忘记密码?</a>
                            </div>
                            <YouMayLove ref='YouMayLove'></YouMayLove>
						</section>
                    </div>
                </section>

                <!-- <recommended ref="recommended"></recommended> -->
                
            </div>
        </div>
    </div>	
</template>

<script type="text/javascript">
  import recommended from './sidebars/recommended.vue'
  import YouMayLove from './sidebars/YouMayLove.vue'
  import router from '../router'
  export default {
    name: 'Login',
    data () {
      return {
        msg: 'meiyou',
        show: false,
        username: '',
        password: ''
      }
    },
    components: {
      recommended: recommended,
      YouMayLove: YouMayLove
    },
    computed: {
      time () {
        return 1651
      }
    },
    methods: {
      print () {
        console.log(123)
      },
      showlist () {
        this.show = true
      },
      login () {
        if (this.username === '' || this.password === '') {
          alert('请将信息填写完整')
        } else {
          this.$http.post(window.baseUrl + '/api/accounts/login/', {
            'username': this.username,
            'password': this.password
          }).then((response) => {
            this.un = response.data.data.username
            if (response.data.status === 1) {
              router.go(0)
              router.push('/')
            } else {
              console.log(123)
            }
          }).catch((response) => {
            alert(response.data.data.msg)
          })
        }
      },
      logout () {
        this.$http.post(window.baseUrl + '/api/accounts/logout/').then((response) => {
          if (response.data.status === 1) {
            router.push('/')
          } else {
            alert(response.data.msg)
          }
        }).catch((response) => {
          alert('退出失败')
        })
      }
    }
}
</script>



