<template>
    <div id="outermain">
    	<div class="container">
            <div class="row">
            
                <section id="maincontent" class="ten columns positionleft">
                	<div class="padcontent">
                
                    	<section class="content" id="product-detail">
                        
                            <div class="breadcrumb"><a href="index.html">Home</a> / 共享资源 / 详情</div>
                            <h1 class="pagetitle">{{resource.name}}</h1>
                            
                            <div class="row">
                            	<div class="one_fourth columns">
                                <div id="pb-right-column">
                                    <div class="image-block">                                               
                                        <div id="imageitems" class="flexslider">
                                            <ul class="slides">
                                                <li>
                                                    <a class="image" :href="resource.resource_picture" target="_blank" data-rel="prettyPhoto[mixed]" >
                                                   		 <img :src="resource.resource_picture">
                                                    </a>
                                                </li>
                                            </ul>
                                        </div>
                                      </div>

                         <!--            <ul id="usefull_link_block">   
                                        <li class="favoriteproducts">喜欢</li>			
                                    </ul> -->
                                </div>
                              </div>
                                
                            	<div class="three_fourth columns">
                                    <p>发布者：{{ masterName }}     &nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp 发布时间：{{resource.create_time}}  </p>
                                    <p>所在社区:{{ communityName }} &nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp 地址：{{resource.pub_address}}</p>
                                    <p> </p>
								                <div class="price"><span>积分：</span>{{ resource.intergration }} </div>
                                <p><span style="float:left" class="cart" @click='addOrder(resource)'>去结算</span><span style="float:right;margin-right:300px;" class="cart" @click='addCart()'>加入购物车</span></p>
                              </div>
                            </div>

                            <div class="separator"></div>
                            <div class="tabcontainer">
                                <ul class="tabs">
                                    <li @click="ShowDes()" >描述</li>
<!--                                     <li @click="ShowPic()">图片</li>
 -->                                    <li @click="ShowComments()">A&Q</li>
                                </ul>
                                <div class="tab-body">
                                    <div id="tab0" class="tab-content" style="width:850px" v-show="showDes">  
                                        <div v-html="resource.description"> </div>
                                    </div>
                                    <div id="tab1" class="tab-content" style="width:850px" v-show="showPic">  
                                        <p>asdk </p>
                                    </div>
                                    <div id="tab2" class="tab-content" style="width:850px" v-show="showComments">  
                                        <ProCom ref='comments' :objectId='resId'></ProCom>
                                    </div>                                    
                                </div>
                            </div>
						            </section>
                    </div>
                </section>
            </div>
        </div>
    </div>
</template>

<script>
import ProCom from './comments/ProductComments.vue'
export default {
  name: 'ProductDetails',
  data () {
    return {
      resourceId: NaN,
      showDes: true,
      showPic: false,
      showComments: false,
      resource: '',
      masterId: '',
      masterName: '',
      communityId: '',
      communityName: '',
      resourceIntergration: '',
      resId: NaN
    }
  },
  components: {
    ProCom: ProCom
  },
  created () {
    this.getProductDetail()
  },
  methods: {
    ShowComments () {
      this.showPic = false
      this.showDes = false
      this.showComments = true
    },
    ShowDes () {
      this.showPic = false
      this.showDes = true
      this.showComments = false
    },
    ShowPic () {
      this.showPic = true
      this.showDes = false
      this.showComments = false
    },
    getProductDetail () {
      this.resourceId = this.$route.params.id
      this.$http.get(window.baseUrl + '/api/resources/' + this.resourceId + '/detail/').then((response) => {
        if (response.data.code === 200) {
          this.resource = response.data.data
          this.masterName = response.data.data.master.username
          this.masterId = response.data.data.master.id
          this.communityId = response.data.data.community.id
          this.communityName = response.data.data.community.name
          this.resourceIntergration = response.data.data.intergration
          this.resId = response.data.data.id
        } else {
          alert('获取消息失败')
          console.log(4566)
        }
      }).catch((response) => {
        alert('获取消息失败！！')
        console.log(456)
      })
    },
    addOrder (resource) {
      if (confirm('确定使用该资源么?')) {
        this.$http.post(window.baseUrl + '/api/order/create/', {
          'data': [resource]
          // 'resource_id': this.resourceId,
          // 'master_id': this.masterId,
          // 'community_id': this.communityId,
          // 'intergration': this.resourceIntergration,
          // 'order_number': 11
        }).then((response) => {
          console.log('ok')
          alert('使用成功,请查看订单了解详情')
        }).catch((response) => {
          console.log('faile')
          alert('失败')
        })
      }
    },
    addCart () {
      this.$http.get(window.baseUrl + '/api/accounts/add_cart/' + this.resourceId + '/').then((response) => {
        if (response.data.code === 200) {
          if (response.data.status === 1) {
            alert('已加入购物车')
          } else {
            alert('物品已在购物车')
          }
        } else {
          console.log(4564)
        }
      }).catch((response) => {
        alert('加入购物车失败')
      })
    }
  }
}
</script>



