<template>
    <div id="outermain">
    	<div class="container">
            <div class="row">
            
                <section id="maincontent" class="ten columns positionleft">
                	<div class="padcontent">
                
                    	<section class="content" id="login">
                        
                            <div class="breadcrumb"> Home / <a>资源信息</a></div>
                            <h1 class="pagetitle">资源信息</h1>
                 
                              <div style="width:550px;margin-left:65px" >
                                 <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
                                    <el-form-item label="资源名称:" prop="name">
                                      <el-input v-model="ruleForm.name" ></el-input>
                                    </el-form-item>

                                    <el-form-item label="资源描述:" prop="description">
                                      <el-input type="textarea" v-model="ruleForm.description" ></el-input>
                                    </el-form-item>

                                    <el-form-item label="发布地:" prop="pub_address">
                                      <el-input v-model="ruleForm.pub_address" ></el-input>
                                    </el-form-item>
                                    <div v-if="pics !== ''" style="margin-left:80px">
                                      <p>原资源介绍图:</p>
                                      <img :src='pics' style="height:220px;width:260px" >
                                    </div>
                                    <el-form-item label="资源介绍图" required>
                                      <el-upload
                                        class="upload-demo"
                                        action="http://127.0.0.1:8000/api/resources/pic/create/"
                                        :on-remove="handleRemove1"
                                        :file-list="resource_picture"
                                        :on-success="update_pic"
                                        list-type="picture">
                                        <el-button size="small" type="primary">点击上传</el-button>
                                        <div slot="tip" class="el-upload__tip">只能上传jpg/png文件</div>
                                      </el-upload>
                                    </el-form-item>

                                    <el-form-item label="资源分类" prop="category">
                                      <el-select v-model="ruleForm.category" placeholder="请选择资源分类">
                                        <div v-for="category in categories">
                                          <el-option :label="category.name" :value="category.id"></el-option>
                                        </div>
                                      </el-select>
                                    </el-form-item>

                                    <el-form-item label="积分:" prop="intergration">
                                      <el-input v-model="ruleForm.intergration" ></el-input>
                                    </el-form-item>   

                                    <el-form-item label="上传文件" required hidden>
                                      <el-upload
                                        class="upload-demo"
                                        action="http://127.0.0.1:8000/api/resources/pic/create/"
                                        :on-remove="handleRemove"
                                        :file-list="fileList2"
                                        :on-success="update_piclist"
                                        list-type="picture">
                                        <el-button size="small" type="primary">点击上传</el-button>
                                        <div slot="tip" class="el-upload__tip">只能上传jpg/png文件</div>
                                      </el-upload>
                                    </el-form-item>
                                    <!-- {{ruleForm}} -->
                                    <el-form-item label="使用截止期限:" required>
                                      <el-col :span="11">
                                        <el-form-item prop="use_time">
                                         <el-date-picker type="date" placeholder="选择日期" v-model="ruleForm.use_time" style="width: 100%;"></el-date-picker>
                                        </el-form-item>
                                      </el-col>
                                    </el-form-item>

                                    <el-form-item label="评价得分:" prop="intergration" v-if = 'ruleForm.rate' >
                                      <el-input v-model="ruleForm.rate" :disabled="true" ></el-input>
                                    </el-form-item> 
                                     <el-form-item label="评价:" prop="intergration" v-if = 'ruleForm.rate_content'>
                                      <el-input v-model="ruleForm.rate_content" :disabled="true"  ></el-input>
                                     </el-form-item> 
                                     <div v-if = 'ruleForm.picList' style="margin-left:80px">
                                      
                                      <div v-for = "picss in ruleForm.picList" v-if = 'ruleForm.picList' >
                                      <p >评价图片:</p>
                                         <img :src='picss' style="height:220px;width:260px" >
                                      </div>
                                    </div>

                                    <el-form-item>
                                      <el-button  @click="submitForm('ruleForm')">创建</el-button>
                                      <el-button  @click="submitFormChange('ruleForm')">修改</el-button>
                                      <el-button @click="resetForm('ruleForm')">重置</el-button>
                                    </el-form-item>
                                  </el-form>
                              </div>
                            <YouMayLove ref='YouMayLove'></YouMayLove>
						        </section>
                  </div>
              </section>
              <recommended ref="recommended"></recommended>
            </div>
        </div>
    </div>	
</template>
<style>
  .el-row {
    margin-bottom: 20px;
    &:last-child {
      margin-bottom: 0;
    }
  }
  .el-col {
    border-radius: 4px;
  }
  .bg-purple-dark {
    background: #99a9bf;
  }
  .bg-purple {
    background: #d3dce6;
  }
  .bg-purple-light {
    background: #e5e9f2;
  }
  .grid-content {
    border-radius: 4px;
    min-height: 36px;
  }
  .row-bg {
    padding: 10px 0;
    background-color: #f9fafc;
  }
</style>

<script>
  import recommended from './sidebars/recommended.vue'
  import YouMayLove from './sidebars/YouMayLove.vue'
  import router from '../router'
  export default {
    data () {
      return {
        resourceId: NaN,
        fileList2: [],
        picList: '',
        communitys: [],
        categories: [],
        pics: '',
        resource_picture: [],
        userinfo: '',
        ruleForm: {
          picList: '',
          pub_address: '',
          description: '',
          intergration: '',
          rate_content: '',
          rate: '',
          resource_picture: '',
          name: '',
          use_time: '',
          category: ''
        },
        rules: {
          name: [
            { required: true, message: '请输入名称' }
          ],
          description: [
            { required: true, message: '请填写描述' }
          ],
          resource_picture: [
            { required: true, message: '请填写email' }
          ],
          pub_address: [
            { required: true, message: '请填写地址' }
          ],
          use_time: [
            { required: true, message: '请填写地址' }
          ],
          category: [
            { required: true, message: '请填写地址' }
          ],
          address: [
            { required: true, message: '请填写地址' }
          ],
          picListf: [
            { required: true, message: '请填写地址' }
          ],
          community: [
            { required: true, message: '请填写身份证' }
          ]
        }
      }
    },
    components: {
      recommended: recommended,
      YouMayLove: YouMayLove
    },
    created () {
      this.getCate()
      this.resourceId = this.$route.params.id
    },
    watch: {
      picList () {
        this.ruleForm.picList = this.picList
      }
    },
    methods: {
      submitForm () {
        this.$http.post(window.baseUrl + '/api/resources/create/', this.ruleForm).then((response) => {
          if (response.data.code === 200) {
            if (response.data.status === 1) {
              if (confirm('ok')) {
                router.push('/publiction')
                // alert(response.data.msg)
              }
            } else {
              alert(response.data.msg)
            }
          } else {
            alert('获取消息失败')
          }
        }).catch((response) => {
          alert('获取消息失败！！')
        })
      },
      submitFormChange () {
        this.resourceId = this.$route.params.id
        this.$http.post(window.baseUrl + '/api/resources/create/' + this.resourceId + '/', this.ruleForm).then((response) => {
          if (response.data.code === 200) {
            if (response.data.status === 1) {
              if (confirm('ok')) {
                router.push('/publiction')
                // alert(response.data.msg)
              }
            } else {
              router.push('/publiction')
            }
          } else {
            alert('获取消息失败')
          }
        }).catch((response) => {
          alert('获取消息失败！！')
        })
      },
      resetForm (formName) {
        this.$refs[formName].resetFields()
      },
      update_piclist (file, fileList) {
        this.picList.push(file.url)
      },
      update_pic (file) {
        console.log(file)
        this.ruleForm.resource_picture = file.url
        // this.ruleForm.resource_picture.filename = file.name
      },
      handleRemove1 (file) {
        var index = this.resource_picture.indexOf(file.url)
        this.resource_picture.splice(index, 1)
      },
      handleRemove (file, fileList) {
        var index = this.picList.indexOf(file.url)
        this.picList.splice(index, 1)
      },
      getCate () {
        this.resourceId = this.$route.params.id
        this.$http.get(window.baseUrl + '/api/resources/list_category/').then((response) => {
          if (response.data.code === 200) {
            this.categories = response.data.data
            this.getResInfo()
          } else {
            console.log(123)
          }
        }).catch((response) => {
          console.log(123)
        })
      },
      getResInfo () {
        this.resourceId = this.$route.params.id
        if (this.resourceId) {
          this.$http.get(window.baseUrl + '/api/resources/' + this.resourceId + '/get_detail/').then((response) => {
            this.ruleForm.category = response.data.data.category
            this.ruleForm.resource_picture = response.data.data.resource_pictures
            this.ruleForm.description = response.data.data.description
            this.ruleForm.intergration = response.data.data.intergration
            this.ruleForm.picList = response.data.data.picList
            this.ruleForm.rate_content = response.data.data.rate_content
            this.ruleForm.rate = response.data.data.rate
            this.ruleForm.name = response.data.data.name
            this.ruleForm.picList = response.data.data.picList
            this.ruleForm.pub_address = response.data.data.pub_address
            this.ruleForm.use_time = response.data.data.use_time
            this.pics = response.data.data.resource_picture
          }).catch((response) => {
            console.log(123)
          })
        } else {
          this.pics = ''
        }
      }
    }
  }
</script>



