<template>
    <div id="outermain">
    	<div class="container">
            <div class="row">
                <section id="maincontent" class="ten columns positionleft">
                	<div class="padcontent">
                    	<section class="content" id="login">
                          <div class="breadcrumb"> Home / <a>评价</a></div>
                          <h1 class="pagetitle">评价</h1>

                          <div class="row" style="margin-left:45px">
                            <div class="one_fourth columns">
                              <div id="pb-right-column">
                                  <div class="image-block">                                               
                                      <div id="imageitems" class="flexslider">
                                          <ul class="slides">
                                              <li>
                                                  <a class="image" :href="resource.resource_picture" target="_blank" data-rel="prettyPhoto[mixed]" >
                                                     <img :src="resource.resource_picture">
                                                  </a>
                                              </li>
                                          </ul>
                                      </div>
                                    </div>
                              </div>
                            </div>
                            <div class="three_fourth columns">
                                    <p>发布者：{{ masterName }}     &nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp 发布时间：{{resource.create_time}}  </p>
                                    <p>所在社区:{{ communityName }} &nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp 地址：{{resource.pub_address}}</p>
                                    <p> </p>
                                <div class="price"><span>积分：</span>{{ resource.intergration }} </div>
                            </div>
                          </div>
                          <br>
                          <div style="width:550px;margin-left:65px" >
                            <el-rate
                              v-model="rate"
                              show-text>
                            </el-rate>
                            <div style="margin: 20px 0;"></div>
                            <el-input
                              type="textarea"
                              :autosize="{ minRows: 2, maxRows: 4}"
                              placeholder="请输入您的评价"
                              v-model="rate_content">
                            </el-input>
                            <!-- {{picList}} -->
                            <el-upload
                              class="upload-demo"
                              action="http://127.0.0.1:8000/api/resources/pic/create/"
                              :on-remove="handleRemove"
                              :file-list="fileList2"
                              :on-success="update_piclist"
                              list-type="picture">
                              <el-button size="small" type="primary">展示你的使用</el-button>
                              <div slot="tip" class="el-upload__tip">只能上传jpg/png文件</div>
                            </el-upload>


                            <div style="margin-left:180px">
                                <el-button  @click="submitForm()">确定</el-button>
                                <el-button >重置</el-button>
                            </div>
                          </div>                              
                          <YouMayLove ref='YouMayLove'></YouMayLove>
						          </section>
                    </div>
                </section>
                <recommended ref="recommended"></recommended>
            </div>
        </div>
    </div>	
</template>

<script>
  import recommended from './sidebars/recommended.vue'
  import YouMayLove from './sidebars/YouMayLove.vue'
  import router from '../router'
  export default {
    data () {
      return {
        resourceId: NaN,
        fileList: [],
        fileList2: [],
        picList: [],
        rate_content: '',
        rate: null,
        resource: '',
        masterId: '',
        masterName: '',
        communityId: '',
        communityName: '',
        resourceIntergration: ''
      }
    },
    components: {
      recommended: recommended,
      YouMayLove: YouMayLove
    },
    created () {
      this.getProductDetail()
    },
    watch: {
      picList () {
        this.fileList = this.picList
      }
    },
    methods: {
      getProductDetail () {
        this.resourceId = this.$route.params.id
        this.$http.get(window.baseUrl + '/api/resources/' + this.resourceId + '/detail/').then((response) => {
          if (response.data.code === 200) {
            this.resource = response.data.data
            this.masterName = response.data.data.master.username
            this.masterId = response.data.data.master.id
            this.communityId = response.data.data.community.id
            this.communityName = response.data.data.community.name
            this.resourceIntergration = response.data.data.intergration
            this.resId = response.data.data.id
          } else {
            alert('获取消息失败')
          }
        }).catch((response) => {
          alert('获取消息失败！！')
        })
      },
      submitForm () {
        this.$http.post(window.baseUrl + '/api/resources/rate_res/', {
          'picList': this.picList,
          'rate_content': this.rate_content,
          'rate': this.rate,
          'res_id': this.resourceId
        }).then((response) => {
          if (response.data.code === 200) {
            if (response.data.status === 1) {
              if (confirm('ok')) {
                router.push('/order')
              }
            } else {
              alert(response.data.msg)
            }
          } else {
            alert('获取消息失败')
          }
        }).catch((response) => {
          alert('获取消息失败！！')
        })
      },
      update_piclist (file) {
        this.picList.push(file.url)
      },
      handleRemove (file, fileList) {
        var index = this.picList.indexOf(file.url)
        this.picList.splice(index, 1)
      }
    }
  }
</script>



