<!-- 							    <el-dropdown-item divided>蚵仔煎</el-dropdown-item>
 -->							  </el-dropdown-menu>
							</el-dropdown>
	                    </div>
	                	<div class="currency" @click='logout()'>退出</div>
	                </div>
	            </div>
	            </div>
	    	</div>
	        
	        <div id="logo-wrapper">
	            <div class="container">
	            <div class="row">
	                <div id="logo" class="six columns">
	                    <a href="/"><img src="../../../static/images/logo.png" alt=""/></a>
	                    <span class="desc">社区资源共享</span>
	                </div>
	                <div class="right six columns">
	                    <form action="#" id="searchform" method="get">
	                        <input type="text"  placeholder="根据资源名称搜索" id="s" name="s" v-model="search" class="field">
	                        <router-link :to="'/product-grid/' + search"><input type="submit" value="" class="searchbutton"></router-link>
	                    </form>

	                    <div id="shopping-cart-wrapper">
	                        <div id="shopping_cart"><!-- <a  id="shop-bag" >我的共享清单(购物车)</a> -->
	                        购物车:<router-link  to='/checkout'><img style="width:20px;height:20px" src="./shopping.png" /></router-link> &nbsp&nbsp
	                        订单:<router-link to='/order'><img style="width:20px;height:20px" src="./order1.png" /></router-link>
