</template>

<script>
  export default {
    name: 'header',
    data () {
      return {
        msg: '',
        show: false,
        username: '',
        password: '',
        isNotLogin: true,
        search: ''
      }
    },
    created () {
      this.getUserName()
    },
    methods: {
      logout () {
        if (confirm('是否退出?')) {
          this.$http.get(window.baseUrl + '/api/accounts/logout/').then((response) => {
            if (response.data.status === 1) {
              alert('已退出')
              this.isNotLogin = true
            }
          }).catch((response) => {
            alert('退出失败')
          })
        }
      },
      getUserName () {
        this.$http.get(window.baseUrl + '/api/accounts/get_username/').then((response) => {
          if (response.data.code === 200) {
            if (response.data.status === 1) {
              this.username = response.data.msg
              this.isNotLogin = false
            } else {
              this.isNotLogin = true
              this.username = ''
            }
          } else {
            this.isNotLogin = true
            this.username = ''
          }
        }).catch((response) => {
          this.isNotLogin = true
          this.username = ''
        })
      }
    }
  }
</script>



