<template>
    <div id="outermain">
        <div class="container">
            <div class="row">
                <!-- <section id="maincontent" class="ten columns positionleft"> -->
                <section id="maincontent" >
                    <div class="padcontent">
                        <section class="content">
                            <div class="breadcrumb"> Home / <a>我的发布</a></div>
                            <h1 class="pagetitle">我的发布</h1>
                            <div class="table_block" id="order-detail-content">
                                <table id="cart_summary">
                                    <thead>
                                        <tr>
                                            <th class="cart_product first_item"><a class="button" v-if="show" id='btn' @click='remove_from_the_cart_all()'>删除</a></th>
                                            <th class="cart_product">图片</th>
                                            <th class="cart_product first_item">资源名</th>
                                            <th class="cart_description item">地址：</th>
                                            <th class="cart_avail item">所在社区</th>
                                            <th class="cart_unit item">积分</th>
                                            <th class="cart_unit item">状态</th>
                                            <th class="cart_total item">操作</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        <tr v-for="resource in resources" class="cart_item" >
                                            <td> <input type="checkbox" v-model="num" id="payment_method_paypal" class="input-radio" name="payment_method" :value="resource"/></td>
                                            <td class="cart_product"><router-link :to="'/product-details/' + resource.id"><img :src='resource.resource_picture' style="height:40px;width:50px"></router-link></td>
                                            <td class="cart_product">
                                                <!-- {{ resource.name }} -->
                                                <router-link :to="'/product-details/' + resource.id">{{resource.name}}</router-link>
                                            </td>
                                            <td class="cart_description">
                                               {{ resource.pub_address }}       
                                            </td>
                                            
                                            <td class="cart_avail">{{ resource.community.name }}</td>
                                            <td class="cart_unit">{{ resource.intergration }}</td>
                                            <td class="cart_unit">
                                              <div v-if='resource.process === 0' >未被使用</div>
                                              <div v-if='resource.process === 1' >锁定使用</div>
                                              <div v-if='resource.process === 2' >未被使用</div>
                                              <div v-if='resource.process === 3' >使用中</div>
                                              <div v-if='resource.process === 4' >已归还</div>
                                              <div v-if='resource.process === 5' >已评价</div>
                                            </td>
                                            <td class="cart_total">
                       
                                                <router-link style='color:gray' :to="'/Release/' + resource.id"><span class="price" id='operation'>查看</span></router-link>
                                                <span class="price" @click='remove_from_the_cart(resource.id)' id='operation'>删除</span>
                                            </td>
                                        </tr>
                                    </tbody>
                            </table>
<!-- 
                            <table   v-for='resource in resources' v-if='resource.id === resRateid'>
                               <thead>
                                <tr>
                                    <th class="cart_product " >资源名</th>
                                    <th class="cart_product">评价得分</th>
                                    <th class="cart_product ">评价内容</th>
                                    <th class="cart_product ">资源名图片</th>
                                </tr>
                              </thead>
                              <thbody >
                                <td class="cart_avail">{{ resource.name }}</td>
                                <td class="cart_avail">{{ resource.rate }}</td>
                                <td class="cart_unit">{{ resource.rate_content }}</td>
                                <td class="cart_unit">
                                  <div v-for='respic in resource.picList'>
                                    <img :src='respic' >
                                  </div>
                                </td>
                              </thbody>
                            </table>
 -->
                          <!--           <center>
                                      <div v-for='resource in resources'>
                                        <div v-if='resource.id === resRateid'> 
                                          <p>资源名:{{resource.name}}</p>
                                          <p>评价得分:{{resource.rate}}</p>
                                          <p>评价内容:{{resource.rate_content}}</p>
                                          <div v-for='respic in resource.picList'>
                                            <img :src='respic' style="height:260px;width320px">
                                          </div>
                                        </div>
                                      </div>
                                      </center> -->

                                    </div>
                            </div>
                            <br><br><br>
                            <button>&nbsp
                              <button>
                                  <div id="payment">
                                      <ul class="payment_methods">
                                          <li>
                                              <label for="payment_method_bacs">共享 </label> 
                                              <div class="payment_box">
                                                  <p>如果你把快乐告诉一个朋友，你将得到两个快乐，而如果你把忧愁向一个朋友倾诉，你将被分掉一半忧愁。</p>
                                              </div>                      
                                          </li>
                                          <li>
                                              <label for="payment_method_cheque">便捷 </label>  
                                              <div class="payment_box">
                                                  <p>无论个人环境有何不同，有一点，他们所生活的世界都是由他们自己造成的。方便自己，方便他人.人之所以痛苦一是求之不得，二是舍之不得。老天在送你一个大礼物时都会用重重困难做包装；任何时候都不要放弃底线，只有心灵站直了生命才不会倾斜；智者把放下当前进，愚者把放下当绝望，放下的高度，就是快乐的程度；一个人快乐的前提不是他有能力改变世界，而是有恒心改变自己。</p>
                                              </div>                  
                                          </li>
                                          <li>
                                              <label for="payment_method_paypal">高效</label>
                                              <div class="payment_box">
                                                  <p>只有那些敢于相信自己体内蕴藏着比周围的人群更为优秀的能力的人，才可能获得灿烂辉煌的成功。</p>
                                              </div>                      
                                          </li>
                                      </ul>
                                  
                                      <div class="form-row">      
                                          <a href="#" class="button">Place order</a>
                                      </div>
                                      <div class="clear"></div>
                                  </div>
                              </button>
                          </button>
                        </section>
                     
                    </div>
                </section>
                
            </div>
        </div>
    </div>
</template>
<style>
#operation:hover{ 
color:red
}

#btn:hover{ 
background-color:red
}
.modal-scale-transition{
  transition: transform,opacity .3s ease;
}

.modal-scale-enter,
.modal-scale-leave {
    opacity: 0;
}

.modal-scale-enter {
  transform: scale(1.1);
}
.modal-scale-leave {
  transform: scale(0.8);
}
</style>
<script>
export default {
  name: 'Checkout',
  data () {
    return {
      resourceId: '',
      resources: '',
      tableData: [{
        name: '2016-05-02',
        rate: '王小虎',
        rate_content: '上海市普陀区金沙江路 1518 弄',
        picList: '阿斯顿'
      }],
      resRateid: '',
      isShow: false,
      sources: 0,
      resourcenNum: 0,
      num: [],
      show: false
    }
  },
  created () {
    this.getPublic()
  },
  watch: {
    num (curVal, oldVal) {
      if (curVal) {
        if (this.num.length >= 0) {
          this.show = true
          this.sources = 0
          this.resourcenNum = this.num.length
          if (this.num.length === 0) {
            this.show = false
            this.sources = 0
          } else {
            for (var i = 0; i < this.num.length; i++) {
              this.sources += this.num[i].intergration
            }
          }
        }
      }
    }
  },
  methods: {
    getPublic () {
      this.$http.get(window.baseUrl + '/api/resources/get_pub/').then((response) => {
        if (response.data.code === 200) {
          this.resources = response.data.data
        } else {
          console.log(456)
        }
      }).catch((response) => {
        console.log(55)
      })
    },
    ordered () {
      this.$http.post(window.baseUrl + '/api/order/create/', {
        'data': this.num
      }).then((response) => {
        if (response.data.code === 200) {
          if (response.data.status === 1) {
            this.getPublic()
          } else {
            alert('请选择要使用的资源')
          }
        } else {
          alert('请选择要使用的资源')
        }
      }).catch((response) => {
        alert('请选择要使用的资源')
      })
    },
    remove_from_the_cart_all () {
      if (this.num.length === 0) {
        alert('no')
      }
      if (confirm('is delete') && this.num.length > 0) {
        var reList = []
        for (var i = 0; i < this.num.length; i++) {
          reList.push(this.num[i].id)
        }
        this.$http.post(window.baseUrl + '/api/resources/delete/', {'pk_list': reList}).then((response) => {
          if (response.data.code === 200) {
              // this.resources = this.resources.filter(t => t.id !== resid)
            this.getPublic()
          } else {
            alert('访问失败')
          }
        }).catch((response) => {
          alert('访问失败')
        })
      }
    },
    remove_from_the_cart (resid) {
      if (confirm('is delete')) {
        this.$http.post(window.baseUrl + '/api/resources/delete/', {'pk_list': [resid]}).then((response) => {
          if (response.data.code === 200) {
              // this.resources = this.resources.filter(t => t.id !== resid)
            this.getPublic()
          } else {
            alert('访问失败')
          }
        }).catch((response) => {
          alert('访问失败')
        })
      }
    },
    lookRatee (res) {
      this.isShow = true
      this.resRateid = res
    },
    orderedOne (res) {
      if (confirm('is ordered')) {
        this.$http.post(window.baseUrl + '/api/order/create/', {
          'data': [res]
        }).then((response) => {
          if (response.data.code === 200) {
            if (response.data.status === 1) {
              this.getPublic()
            } else {
              alert('失败')
            }
          } else {
            alert('访问失败')
          }
        }).catch((response) => {
          alert('访问失败！')
        })
      }
    }
  }
}
</script>



