<script>
export default {
  data () {
    return {
      msg: '',
      resources: '',
      show1: true,
      show2: false,
      showAdd: true,
      showremove: '',
      focus: '',
      i: -1
    }
  },
  created () {
    this.getUserfocus()
    this.getLeasted5()
  },
  methods: {
    getLeasted5 () {
      this.$http.get(window.baseUrl + '/api/resources/get_leasted_5/').then((response) => {
        if (response.data.code === 200) {
          if (response.data.status === 1) {
            if (this.focus !== '') {
              for (let res of response.data.data) {
                for (let focu of this.focus) {
                  if (focu.id === res.id) {
                    res.add = 0
                  }
                }
              }
              this.resources = response.data.data
            } else {
              this.resources = response.data.data
            }
          } else {
            console.log(123)
          }
        } else {
          console.log(123123)
        }
      }).catch((response) => {
        console.log(456)
      })
    },
    addCart (resid) {
      this.$http.get(window.baseUrl + '/api/accounts/add_cart/' + resid + '/').then((response) => {
        if (response.data.code === 200) {
          if (response.data.status === 1) {
            alert('成功加入购物车')
          } else {
            alert('物品已在购物车')
          }
        } else {
          console.log(4564)
        }
      }).catch((response) => {
        alert('加入购物车失败')
      })
    },
    isShow () {
      if (this.show1 === true) {
        this.show1 = false
        this.show2 = true
      } else {
        this.show2 = false
        this.show1 = true
      }
    },
    showAdd (index) {
      this.i = index
    },
    getUserfocus () {
      this.$http.get(window.baseUrl + '/api/accounts/get_username/').then((response) => {
        if (response.data.code === 200) {
          if (response.data.status === 1) {
            this.focus = response.data.data
            this.getLeasted5()
          } else {
            this.focus = ''
          }
        } else {
          this.focus = ''
          alert('您还没有登录.请登录')
        }
      }).catch((response) => {
        this.focus = ''
      })
    },
    focusRes (resid) {
      this.$http.post(window.baseUrl + '/api/resources/' + resid + '/focus/').then((response) => {
        if (response.data.code === 200) {
          if (response.data.status === 1) {
            this.focus = response.data.data
            this.getUserfocus()
          } else {
            this.focus = ''
            alert(response.data.msg)
          }
        } else {
          this.focus = ''
          alert(response.data.msg)
        }
      }).catch((response) => {
        this.focus = ''
        alert(response.data.msg)
      })
    },
    cancleFocusRes (resid) {
      this.$http.post(window.baseUrl + '/api/resources/' + resid + '/cancle_foc/').then((response) => {
        if (response.data.code === 200) {
          if (response.data.status === 1) {
            this.focus = response.data.data
            this.getUserfocus()
          } else {
            this.focus = ''
          }
        } else {
          this.focus = ''
        }
      }).catch((response) => {
        this.focus = ''
      })
    }
  }
}
