<template>
    <div id="outermain">
    	<div class="container">
            <div class="row">
                <!-- <section id="maincontent" class="ten columns positionleft"> -->
                <section id="maincontent" >
                	<div class="padcontent">
                
                    	<section class="content">
                        
                            <div class="breadcrumb">Home / <a>使用清单</a></div>
                            <h1 class="pagetitle">使用清单</h1>

                            <div class="table_block" id="order-detail-content">
                                <table id="cart_summary">
                                    <thead>
                                        <tr>
                                            <th class="cart_product first_item"><a :class="{button: true, exclusive: false}" id='btn' v-if="show" style='float:right; margin-right:10px'  @click="deleteOrder">删除</a></th>
                                            <th class="cart_product first_item">图片</th>
                                            <th class="cart_product first_item">订单号</th>
                                            <th class="cart_description item">名称</th>
                                            <th class="cart_ref item">状态.</th>
                                            <th class="cart_avail item">所在社区</th>
                                            <th class="cart_unit item">积分</th>
                                            <th class="cart_total item">操作</th>
                                        </tr>
                                    </thead>

                                    <tbody>
                                        <tr v-for="order in orders" class="cart_item" >
                                            <td> <input type="checkbox" v-model='num' id="payment_method_paypal" class="input-radio" name="payment_method" :value="order.id"  /></td>
                                            <td class="cart_product"><router-link :to="'/product-details/' + order.resource.id"><img :src='order.resource.resource_picture' style="height:37px;width:50px"></router-link></td>

                                            <td class="cart_product">
                                                {{ order.order_number }}
                                            </td>
                                            <td class="cart_description">
                                               <router-link :to="'/product-details/' + order.resource.id">{{ order.resource.name }} </router-link>	
                                            </td>
                                            <td class="cart_ref" v-if="order.process === 1">锁定使用</td>
                                            <td class="cart_ref" v-if="order.process === 2">订单关闭 </td>
                                            <td class="cart_ref" v-if="order.process === 3">使用中 </td>
                                            <td class="cart_ref" v-if="order.process === 4"> 归还</td>
                                            <td class="cart_ref" v-if="order.process === 5"> 已评价</td>
                                            <td class="cart_avail">{{ order.community.name }}</td>
                                            <td class="cart_unit">{{ order.resource.intergration }}</td>
                                            <td class="cart_total">
                                                <span id='operation' v-if="order.process === 1" @click='useOrder(order.id)'>使用</span>
                                                <span id='operation' v-if="order.process === 3" @click='returnOrder(order.id)'>归还</span>
                                                <span id='operation' v-if="order.process === 4 || order.process === 2 || order.process === 5" @click='deleteOrderSingle(order.id)'>删除</span>
                                                <span id='operation' v-if="order.process !== 2 && order.process !== 4 && order.process !== 5" @click='cancelOrder(order.id)'>取消</span>
                                                <router-link v-if="order.process === 4" :to="'/Ratee/' + order.resource.id" class="more">评价</router-link>
                                            </td>
                                        </tr>
                                    </tbody>

                                    <tfoot>
                                        <tr class="cart_total price">
                                            <td colspan="2" class="none1"></td>
                                            <td class="none2">  </td>
                                            <td class="none2">  </td>
                                            <td class="none3"> <div v-on:click='getPrevious()' id='operation' style="float:right"> 上一页</div></td>
                                            <td colspan="2"><div v-on:click='getNext()' id='operation' style="float:left"> 下一页</div></td>
                                            <td id="total_product" class="price"></td>
                                        </tr>
                                        <tr class="cart_total price">
                                        	<td colspan="2" class="none1"> </td>
                                            <td class="none2"> </td>
                                            <td class="none3"> </td>
                                            <td class="none2"> </td>
                                            <td colspan="2">资源总数：</td>
                                            <td id="total_product" class="price">{{ resourcenNum }}</td>
                                        </tr>
                                        <tr class="cart_total delivery">
                                        	<td colspan="2" class="none1"> </td>
                                            <td class="none2"> </td>
                                            <td class="none2">  </td>
                                            <td class="none3"> </td>
                                            <td colspan="2"  @click="getMyOrders()">所需积分：</td>
                                            <td id="total_shipping" class="price">{{ sources }}</td>
                                        </tr>								
                                    </tfoot>
                                    </table>
                            </div>
							
         <!--                    <a class="button" style='float:right;  margin-right:50px' id='btn' >取消</a>&nbsp;
                            <a :class="{button: true, exclusive: false}" id='btn' style='float:right; margin-right:10px'>确定</a> -->
                             
                            <br><br><br>


                            <button>&nbsp
                                <button>
                                    <div id="payment">
                                        <ul class="payment_methods">
                                            <li>
                                                <label for="payment_method_bacs">共享 </label> 
                                                <div class="payment_box">
                                                    <p>如果你把快乐告诉一个朋友，你将得到两个快乐，而如果你把忧愁向一个朋友倾诉，你将被分掉一半忧愁。</p>
                                                </div>						
                                            </li>
                                            <li>
                                                <label for="payment_method_cheque">便捷 </label> 	
                                                <div class="payment_box">
                                                    <p>无论个人环境有何不同，有一点，他们所生活的世界都是由他们自己造成的。方便自己，方便他人.人之所以痛苦一是求之不得，二是舍之不得。老天在送你一个大礼物时都会用重重困难做包装；任何时候都不要放弃底线，只有心灵站直了生命才不会倾斜；智者把放下当前进，愚者把放下当绝望，放下的高度，就是快乐的程度；一个人快乐的前提不是他有能力改变世界，而是有恒心改变自己。</p>
                                                </div>  				
                                            </li>
                                            <li>
                                                <label for="payment_method_paypal">高效</label>
                                                <div class="payment_box">
                                                    <p>只有那些敢于相信自己体内蕴藏着比周围的人群更为优秀的能力的人，才可能获得灿烂辉煌的成功。</p>
                                                </div>   					
                                            </li>
                                        </ul>
                                    
                                        <div class="form-row">		
                                            <a href="#" class="button">Place order</a>
                                        </div>
                                        <div class="clear"></div>
                                    </div>
                                </button>
                            </button>
						          </section>
                    </div>
                </section>
                
            </div>
        </div>
    </div>
</template>
<style>
#operation:hover{ 
color:red
}

#btn:hover{ 
background-color:red
}
.modal-scale-transition{
  transition: transform,opacity .3s ease;
}

.modal-scale-enter,
.modal-scale-leave {
    opacity: 0;
}

.modal-scale-enter {
  transform: scale(1.1);
}
.modal-scale-leave {
  transform: scale(0.8);
}
</style>
<script>
export default {
  name: 'Checkout',
  data () {
    return {
      resourceId: '',
      orders: '',
      sources: 0,
      resourcenNum: 0,
      showing: false,
      num: [],
      pagePrevious: '',
      pageNext: '',
      show: false
    }
  },
  created () {
    this.getMyOrders()
  },
  watch: {
    num (curVal, oldVal) {
      if (curVal) {
        if (this.num.length >= 0) {
          this.show = true
          this.sources = 0
          this.resourcenNum = this.num.length
          if (this.num.length === 0) {
            this.sources = 0
            this.show = false
          } else {
            for (var i = 0; i < this.num.length; i++) {
              var a = this.orders.filter(k => k.id === this.num[i])
              this.sources += a[0].intergration
            }
          }
        }
      }
    }
  },
  methods: {
    getCartList () {
      this.$http.get(window.baseUrl + '/api/accouasdnts/get_cart_list/').then((response) => {
        if (response.data.code === 200) {
          this.resources = response.data.data
        } else {
          console.log(456)
        }
      }).catch((response) => {
        console.log(55)
      })
    },
    getMyOrders () {
      this.$http.get(window.baseUrl + '/api/order/get_my_orders/').then((response) => {
        if (response.data.code === 200) {
          this.orders = response.data.data
          this.pageNext = response.data.next
          this.pagePrevious = response.data.previous
        } else {
          console.log(456)
        }
      }).catch((response) => {
        console.log(789)
      })
    },
    getNext () {
      if (this.pageNext === null) {
        alert('这是最后一页')
      } else {
        this.$http.get(this.pageNext).then((response) => {
          if (response.data.code === 200) {
            this.orders = response.data.data
            this.pageNext = response.data.next
            this.pagePrevious = response.data.previous
          } else {
            alert('这是最后一页')
          }
        }).catch((response) => {
          alert('这是最后一页')
        })
      }
    },
    getPrevious () {
      if (this.pagePrevious === null) {
        alert('这是最前面的一页')
      } else {
        this.$http.get(this.pagePrevious).then((response) => {
          if (response.data.code === 200) {
            this.orders = response.data.data
            this.pageNext = response.data.next
            this.pagePrevious = response.data.previous
          } else {
            alert('这是最前面的一页')
          }
        }).catch((response) => {
          alert('这是最前面的一页')
        })
      }
    },
    deleteOrder () {
      if (confirm('是否删除订单?')) {
        this.$http.post(window.baseUrl + '/api/order/delete/', {
          'pk_list': this.num
        }).then((response) => {
          if (response.data.code === 200) {
            if (response.data.status === 1) {
              this.getMyOrders()
              this.sources = 0
              this.resourcenNum = 0
            } else {
              alert('访问失败1')
            }
          } else {
            alert('访问失败2')
          }
        }).catch((response) => {
          alert('访问失败3')
        })
      }
    },
    deleteOrderSingle (orderid) {
      if (confirm('是否删除订单?')) {
        this.$http.post(window.baseUrl + '/api/order/delete/', {
          'pk_list': [orderid]
        }).then((response) => {
          if (response.data.code === 200) {
            if (response.data.status === 1) {
              this.getMyOrders()
            } else {
              alert('访问失败1')
            }
          } else {
            alert('访问失败2')
          }
        }).catch((response) => {
          alert('访问失败3')
        })
      }
    },
    cancelOrder (orderid) {
      if (confirm('是否取消订单?')) {
        this.$http.post(window.baseUrl + '/api/order/cancel/', {
          'order_id': orderid
        }).then((response) => {
          if (response.data.code === 200) {
            if (response.data.status === 1) {
              this.getMyOrders()
            } else {
              alert(response.data.msg)
            }
          } else {
            alert('访问失败2')
          }
        }).catch((response) => {
          alert('访问失败3')
        })
      }
    },
    useOrder (orderid) {
      if (confirm('确定使用资源?')) {
        this.$http.post(window.baseUrl + '/api/order/order_operation/', {
          'order_id': orderid,
          'process': 3
        }).then((response) => {
          if (response.data.code === 200) {
            if (response.data.status === 1) {
              this.getMyOrders()
            } else {
              alert(response.data.msg)
            }
          } else {
            alert('访问失败2')
          }
        }).catch((response) => {
          alert('访问失败3')
        })
      }
    },
    returnOrder (orderid) {
      if (confirm('是否归还资源?')) {
        this.$http.post(window.baseUrl + '/api/order/return_back/', {
          'order_id': orderid
        }).then((response) => {
          if (response.data.code === 200) {
            if (response.data.status === 1) {
              this.getMyOrders()
            } else {
              alert(response.data.msg)
            }
          } else {
            alert('访问失败2')
          }
        }).catch((response) => {
          alert('访问失败3')
        })
      }
    }
  }
}
</script>



