<template>
	<div class="wp-pagenavi">
		<p v-on:click="get_page()">123123</p>
		<router-link to='/blog'>{{pageData.pageNext}}</router-link>
		<!-- <a href="#" class="page">1</a><span ><span>2</span></span><a href="#" class="page">3</a> &nbsp;&nbsp;当前页 1 of 3 -->
	</div>
</template>

<script type="text/javascript">
import router from '../../router'
export default {
  name: 'Page',
  data () {
    return {
      isActive: false,
      count: ''
    }
  },
  props: {
    pageData: String
  },
  methods: {
    get_page () {
      console.log(666)
      console.log(this.pageData)
      this.$http.get(this.pageData.pageNext).then((response) => {
        if (response.data.code === 200) {
          this.articleList = response.data.data
          this.rawHtml = response.data.data.text
          this.pagedata = {
            'pageCount': response.data.count,
            'pageNext': response.data.next,
            'pagePrevious': response.data.previous
          }
          router.push('/blog')
        } else {
          console.log(555)
        }
      }).catch((response) => {
        console.log(16546)
        console.log(response.data)
      })
    }
  }
}
</script>



