    methods: {
      submitForm (formName) {
        this.ruleForm.region = this.region
        let that = this
        this.$http.post(window.baseUrl + '/api/accounts/updata_user/', this.ruleForm).then((response) => {
          if (response.data.code === 200) {
            if (response.data.status === 1) {
              // this.ruleForm = response.data.data
              that.is_dis = false
              // alert('修改成功')
              router.go('/')
            } else {
              console.log(123)
            }
          } else {
            console.log(123)
          }
        }).catch((response) => {
          console.log(123)
        })
      },
      resetForm (formName) {
        this.$refs[formName].resetFields()
      },
      getCom () {
        this.resourceId = this.$route.params.id
        this.$http.get(window.baseUrl + '/api/community/list/').then((response) => {
          this.communitys = response.data.results
        }).catch((response) => {
          console.log(123)
        })
      },
      getUserInfo () {
        this.$http.get(window.baseUrl + '/api/accounts/userinfo/').then((response) => {
          if (response.data.code === 200) {
            if (response.data.status === 1) {
              this.ruleForm = response.data.data
              this.region = response.data.data.region
            } else {
              console.log(123)
            }
          } else {
            console.log(123)
          }
        }).catch((response) => {
          console.log(123)
        })
      },
      update_userinfo () {
        this.is_dis
      }
    }
