<template>
        <div id="outermain">
        	<div class="container">
                <div class="row">
                    <section id="maincontent" class="ten columns positionleft">
                    	<div class="padcontent">
                        	<section class="content">
                                <div class="breadcrumb"><a href="index.html">Home</a> / 咨询公告</div>
                                <h1 class="pagetitle">{{ title }}</h1>
                                  <!--   <div v-html="rawHtml"><ul><li>asdasd</li></ul></div>
                                    <div >{{msg}}</div>
                                    <input type="text" v-model="msg" name=""> -->
                                <article class="post">
                                        <div class="postimg">
                                            <img v-bind:src="cover_img" style=" width:300px; height:280px">
                                        </div>
                                        <div class="entry-content">                                            
                                            <div class="entry-utility">
                                                {{ dateline }} / <a>{{ category }} </a> 
                                            </div>
                                            <!-- <h2 class="posttitle"><a>{{ title }}</a></h2> -->                                            
                                            <div v-html="text"></div>
                                        </div>
                                    <div class="clear"></div>
                                </article>                                
                                <div class="author">
                                	<h3>关于公告</h3>
                                    <span class="alignleft frame"><img class="avatar" alt="" src="../../static/images/content/author.jpg" ></span>
                                    <cite class="fn">社区服务助手 </cite>
                                    <p>若有疑问，请到社区具体咨询，若我们有什么不足，欢迎指正，我们将做的更好</p>
                                    <div class="clear"></div>
                                </div>
                                <comments ref='comments' :objectId='articleId'></comments>
							              </section>
                        </div>
                    </section>
<!--                     <recommended ref="recommended" :objectId=></recommended>
 -->                </div>
            </div>
        </div>
</template>
<script>
  import recommended from './sidebars/recommended.vue'
  import comments from './comments/comments.vue'
  export default {
    name: 'ArticleDetail',
    data () {
      return {
        rawHtml: '<div><a>asdas</a><div>',
        show: false,
        msg: '',
        article_id: '',
        author: '',
        category: '',
        cover_img: '',
        dateline: '',
        id: '',
        is_top: '',
        text: '',
        title: '',
        articleId: NaN
      }
    },
    components: {
      recommended: recommended,
      comments: comments

    },
    computed: {
      time () {
        return 1651
      }
    },
    created () {
      this.getArticle()
    },
    methods: {
      showlist () {
        this.show = true
      },
      getArticle () {
        let that = this
        that.article_id = this.$route.params.id
        this.$http.get(window.baseUrl + '/api/article/' + that.article_id + '/detail/').then((response) => {
          that.title = response.data.data.title
          that.text = response.data.data.text
          that.dateline = response.data.data.dateline
          that.cover_img = response.data.data.cover_img
          that.category = response.data.data.category[0].name
          that.author = response.data.data.author
          this.articleId = response.data.data.id
        }).catch((response) => {
          console.log(456)
        })
      }

    }
}
</script>



