<template>
	<section id="comment">
	    <h3>发表你的评论吧:</h3>
		<div style="width:900px;hight:300px;margin-top:50px;margin-bottom:20px">
			<textarea v-model="artCommentText" NAME="user_pass" TYPE="text" SIZE="29" rows="6" cols="80" 
			onpropertychange="if(this.scrollHeight>80) this.style.posHeight=this.scrollHeight+5" 
			onmouseover="this.style.borderColor='#ADFF2F';this.style.backgroundColor='#F0FFFF'"  
			onmouseout="this.style.borderColor='#BFEFFF';this.style.backgroundColor='#ffffff'" 
			style="border-color=#BFEFFF;width: 106; height: 21">  </textarea> <br>
			<a style="margin-right:100px;float:right" @click="commentArtice()" class="more">发表评论</a></span>
        </div>
        <h3>看看别人怎么说</h3>
	    <ol class="commentlist">
	    	<div v-for="comment in comments">
		        <li>
		            <div class="comment-body">
		                <div class="avatar-img"><img src="../../../static/images/content/avatar.gif" alt="" class="avatar frame"/></div>
		                <cite class="fn" v-on:click="getComments()">{{comment.author.username}}</cite>
		                <div class="dr">
		                    <span class="tdate">{{comment.dateline}} &nbsp;</span> &nbsp;-&nbsp; <span @click="showText(comment.id)"><a v-on:click="showCommentText=!showCommentText">回复</a></span>
		                </div>
		                <div class="commenttext">
		                    <p>{{comment.text}}</p>
		                </div>
		                <!-- <div  v-on:click="show=!show" style="float:left;margin-left:10px"> -->
		                <a @click="showchild(comment.id)">展开其他回复</a>

				        <div v-show = "comment.id === i && showCommentText "" style="width:900px;hight:300px;margin-top:50px">
				        	<p>赶紧的评论吧:</p>
							<textarea v-model="commentText" NAME="user_pass" TYPE="text" SIZE="29" rows="6" cols="80" 
							onpropertychange="if(this.scrollHeight>80) this.style.posHeight=this.scrollHeight+5" 
							onmouseover="this.style.borderColor='#ADFF2F';this.style.backgroundColor='#F0FFFF'"  
							onmouseout="this.style.borderColor='#BFEFFF';this.style.backgroundColor='#ffffff'" 
							style="border-color=#BFEFFF;width: 106; height: 21">  </textarea> <br>
							<a style="margin-right:100px;float:right" @click="commentq(comment.id)" class="more">发表评论</a></span>
				        </div>
		                <div v-for="childcomment in childComments" v-show="childcomment.object_id === comment.id && comment.id === j">
				            <ol>
				                <li><div class="com-line"></div>
				                    <div class="comment-body">
				                        <div class="avatar-img"><img src="../../../static/images/content/avatar.gif" alt="" class="avatar frame"/></div>
				                        <cite class="fn"><a>{{childcomment.author.username}}</a></cite>&nbsp;
				                        <div class="dr">
				                            <span class="tdate">{{childcomment.dateline}}  &nbsp;
				                        </div>
				                        <div class="commenttext">
				                            <p>{{childcomment.text}}</p>
				                        </div>
				                    </div>
				                </li>
				            </ol>		                	
		                </div >
		            </div>
		        </li>
	        </div>
	    </ol>

	</section>
</template>

<style>  
#oText{border:1px dotted #ff0000;ryo:expression_r(onfocus=function light (){with(document.all.oText){style.borderColor=(style.borderColor=="#ffee00"?"#ff0000":"#ffee00");
	timer=setTimeout(light,500);}},onblur=function(){this.style.borderColor="#ff0000";clearTimeout(timer)})};  
</style>  

<script>
export default {
  name: 'comments',
  data () {
    return {
      comments: '',
      childComments: '',
      show: false,
      commentText: '',
      artCommentText: '',
      showCommentText: false,
      cc: false,
      i: -1,
      j: -1
    }
  },
  props: {
    objectId: Number
  },
  methods: {
    getCommentsComent (commentId) {
      this.$http.get(window.baseUrl + '/api/comments/' + commentId + '/get_comment_comments/').then((response) => {
        if (response.data.code === 200) {
          if (response.data.status === 1) {
            this.childComments = response.data.data
          } else {
            alert('访问失败')
          }
        }
      }).catch((response) => {
        console.log(756)
      })
    },
    showchild (commentId) {
      this.isShowCommentText(commentId)
      this.getCommentsComent(commentId)
    },
    isShowCommentText (commentId) {
      this.j = this.j === commentId ? -1 : commentId
    },
    getComments () {
      this.$http.get(window.baseUrl + '/api/article/' + this.objectId + '/comment_list/').then((response) => {
        this.comments = response.data.data
        // for (var k = 0; k < this.comments.length; k++) {
        //   this.getCommentsComent(this.comments[k].id)
        // }
      })
    },
    commentArtice () {
      this.$http.post(window.baseUrl + '/api/article/' + this.objectId + '/comment_art/', {'text': this.artCommentText}).then((response) => {
        // this.comments = response.data.data
        this.getComments()
      }).catch((response) => {
        console.log(756)
      })
    },
    commentq (commentId) {
      this.$http.post(window.baseUrl + '/api/comments/comment/' + commentId + '/', {'text': this.commentText}).then((response) => {
        // this.comments = response.data.data
        this.commentText = ''
        this.getComments()
        this.getCommentsComent(commentId)
      }).catch((response) => {
        console.log(756)
      })
      console.log(this.i)
    },
    showText (index) {
      this.i = index
      // this.showCommentText = this.showCommentText === true ? this.cc : true
    },
    getChildComment () {
      this.comments.forEach(function (value, index, array) {
        this.getCommentsComent(value.id)
      })
    }
  },
  watch: {
    objectId (curVal, oldVal) {
      if (curVal) {
        this.getComments()
      }
    }
  }
}

</script>



