<template>
  <div >
        <div id="outermain">
        	<div class="container">
                <div class="row">
                
                    <section id="maincontent" class="ten columns positionleft">
                    	<div class="padcontent">
                    
                        	<section class="content">
                            
                                <div class="breadcrumb"><a href="index.html">Home</a> / 咨询公告</div>
                                <h1 class="pagetitle">Our 咨询公告</h1>
                                <div v-for="article in articleList">
                                    <article class="post">
                                    	<div class=" row">
                                            <div class="postimg five columns">
                                                <img alt="" :src="article.cover_img"  style='height:230px;width:260px'>
                                            </div>
                                            
                                            <div class="seven columns">
                                                <div class="entry-content">
                                                    
                                                    <div class="entry-utility">
                                                        {{ article.dateline }} / <a href="#">{{ article.category[0].name }}
                                                    </div>
                                                    <h2 class="posttitle"><a href="#">{{ article.title }}</a></h2>
                                                    
                                                    <p>
                                                    <div class='showAritcleContent' v-html="article.text">  </div>
                                                    </p>
                                                    
                                                    <router-link class="button" :to="'/article-detail/' + article.id">更多</router-link>
                                                </div>
                                            </div>
                                        </div>    
                                        <div class="clear"></div>
                                    </article>
                                </div>
                                <!-- <page ref='page' :pageData="pagedata"></page> -->
                                <p>
                                <div v-on:click='getPrevious()' style="float:left"> 上一页</div>
                                <div v-on:click='getNext()'style="float:right"> 下一页</div>
                                </p>
							            </section>
                         
                        </div>
                    </section>
            
                    
                </div>
            </div>
        </div>
  </div>
</template>

<style type="text/css">
    .showAritcleContent{
      width:500px; 
      height:80px;
      overflow:hidden;
    }

</style>
<script>
    import page from './page/Page.vue'
    export default {
      name: 'myBlog',
      data () {
        return {
          rawHtml: '',
          articleList: '',
          pagedata: '',
          pageNext: '',
          pagePrevious: '',
          pageCount: ''
        }
      },
      components: {
        page: page
      },
      created () {
        this.getArticleList()
      },
      methods: {
        getArticleList () {
          this.$http.get(window.baseUrl + '/api/article/').then((response) => {
            if (response.data.code === 200) {
              this.articleList = response.data.data
              this.rawHtml = response.data.data.text
              this.pageNext = response.data.next
              this.pagePrevious = response.data.previous
              this.pagedata = {
                'pageCount': response.data.count,
                'pageNext': response.data.next,
                'pagePrevious': response.data.previous
              }
            } else {
              console.log(555)
            }
          }).catch((response) => {
            console.log(16546)
            console.log(response.data)
          })
        },
        getNext () {
          if (this.pageNext === null) {
            alert('no next')
          } else {
            this.$http.get(this.pageNext).then((response) => {
              if (response.data.code === 200) {
                this.articleList = response.data.data
                this.rawHtml = response.data.data.text
                this.pageNext = response.data.next
                this.pagePrevious = response.data.previous
              } else {
                alert(' no next')
              }
            }).catch((response) => {
              alert(' no next')
            })
          }
        },
        getPrevious () {
          if (this.pagePrevious === null) {
            alert('no previous')
          } else {
            this.$http.get(this.pagePrevious).then((response) => {
              if (response.data.code === 200) {
                this.articleList = response.data.data
                this.rawHtml = response.data.data.text
                this.pageNext = response.data.next
                this.pagePrevious = response.data.previous
              } else {
                alert(' no previous')
              }
            }).catch((response) => {
              alert(' no previous')
            })
          }
        }
      }
    }
</script>



