<template>
	<div id="outermain">
		<div class="container">
	        <div class="row">
				<!-- start left-->
	            <section id="maincontent" class="ten columns positionleft">
	            	<div class="padcontent">

	                	<section class="content" id="product-container">

	                        <div class="breadcrumb"><a>Home</a> / 共享资源 </div>
	           				<!-- <img src="../../static/images/content/clothing.jpg" alt=""/> -->
                    <img src="./s6.jpg" style="width:760px;height:275px" />
                    <!-- <img style="width:730px;height:320px" src="./res.jpg" alt=""/> -->

   <!--                    <div class="frame" style="width:630px;height:350px;overflow:hidden;border:0px"> 
                        <div  style="width:800px;height:100px;margin:-55px 0px 0px -220px;"> 
                          <iframe src="https://douban.fm/"  width="960" height="280" scrolling="no"></iframe> 
                        </div> 
                      </div>  -->
<!--                       <iframe id="runtime" name="runtime" width="500" height="250" scrolling="no" frameborder="0" src="https://douban.fm/"></iframe>
 -->
	                        <div class="sortPagiBar">
	                        <form action="" class="productsSortForm">
	                            <p class="select">
	                                <label for="selectPrductSort">排序</label>
	                                <select class="selectProductSort"  v-model="selected">
	                                    <option value="create_time">系统排序</option>
	                                    <!-- <option value="intergration">积分最低</option> -->
	                                    <option value="intergration">积分最高</option>
	                                    <!-- <option value="focus">最受欢迎</option> -->
	                                    <option value="create_time">最新</option>
	                               	</select>
	                            </p>
	                        </form>

	                       <a  class="button"  @click='sortRes()'>确定</a>

	                        </div>
     <!--                      <el-row :gutter="20">
                            <el-col :span="6" v-for="resource in resourses"><div class="grid-content ">{{resource.name}}</div></el-col>
                            <el-col :span="6"><div class="grid-content bg-purple"></div></el-col>
                            <el-col :span="6"><div class="grid-content bg-purple"></div></el-col>
                            <el-col :span="6"><div class="grid-content bg-purple"></div></el-col>
                          </el-row> -->
	                        <!-- start resource-->
	                        <div class="row">
	                        	<div v-for="resource in resourses">
		                            <div class="one_fourth columns" style="margin-bottom:0px;padding: 10px;">
		                                <div class="product-wrapper">
		                                	<!-- <span class="new"></span> -->
		                                    <router-link :to="'/product-details/' + resource.id"><img style='width:270px; height:124px'  v-bind:src="resource.resource_picture"/></router-link>
		                                    <h3 style="height:18px"> <router-link :to="'/product-details/' + resource.id">{{resource.name}}</router-link></h3>
		                                    <div class="price-cart-wrapper">
		                                        <div class="price">
		                                            积分：{{ resource.intergration }}
		                                        </div>
		                                        <div class="cart">
                                              <span >
                                                  <img @click="cancleFocusRes(resource.id)" v-if='resource.add === 0' style="width:13px;height:12px;margin-right:6px" src='./remove.png' > 
                                                  <img v-else  @click="focusRes(resource.id)" style="width:13px;height:12px;margin-right:6px" src='./add.png' > 
                                              </span>
		                                           <router-link :to="'/product-details/' + resource.id" class="more">more</router-link>| <span  @click="addCart(resource.id)" class="buy">buy </span></router-link>
		                                        </div>
		                                        <div class="clear"></div>
		                                    </div>
		                                </div>
		                            </div>
	                            </div>
	                        </div>
	                        <!-- end resource-->
	                        <!-- start page-->
	                        <div v-on:click='getPrevious()' style="float:left"> 上一页</div>
	                        <div v-on:click='getNext()'style="float:right"> 下一页</div>
	                        <!-- end page-->
						</section>
	                </div>
	            </section>
	            <!-- end left-->
	            <!-- start right-->
	            <aside class="two columns">
	            	<div class="sidebar">
	                <ul>
	                    <li class="widget-container">
	                        <h2 class="widget-title">分类</h2>
	                        <ul id ='app-2' v-for="category in categories">
	                            <li ><a @click='getResByCate(category.id)'>{{category.name}}</a>
	                            </li>
	                        </ul>
	                    </li>
                     </ul>
	                </div>
	            </aside>
	            <!-- end right-->
	        </div>
	    </div>
	</div>
</template>


<script>
  export default {
    name: 'ProductGrid',
    data () {
      return {
        resname: '',
        msg: '',
        show: false,
        resourses: '',
        pageNext: '',
        pagePrevious: '',
        pageCount: '',
        resourceId: Number,
        resId: Number,
        focus: '',
        selected: '',
        categories: ''
      }
    },
    computed: {
      time () {
        return 1651
      }
    },
    created () {
      // this.getResources()
      this.getUserfocus()
      this.getResCategory()
      // this.getSearchResources()
      // this.getResources()
    },
    beforeRouteEnter (to, from, next) {
      next(vm => {
        console.log(to.path)
        console.log(from.path)
        if (to.path === '/product-grid') {
          vm.getResources()
        } else {
          vm.getSearchResources()
        }
      })
    },
    methods: {
      print () {
        console.log(123)
      },
      showlist () {
        this.show = true
      },
      getUserfocus () {
        this.$http.get(window.baseUrl + '/api/accounts/get_username/').then((response) => {
          if (response.data.code === 200) {
            if (response.data.status === 1) {
              this.focus = response.data.data
              this.getResources()
            } else {
              this.focus = ''
            }
          } else {
            this.focus = ''
            // alert('您还没有登录.请登录')
          }
        }).catch((response) => {
          this.focus = ''
          alert('您还没有登录.请登录')
        })
      },
      getSearchResources () {
        this.resname = this.$route.params.resname
        this.$http.post(window.baseUrl + '/api/resources/search/', {'s': this.resname}).then((response) => {
          if (response.data.code === 200) {
            if (this.focus !== '') {
              for (let res of response.data.data) {
                for (let focu of this.focus) {
                  if (focu.id === res.id) {
                    res.add = 0
                    console.log(res.add)
                  }
                }
              }
              this.resourses = response.data.data
            } else {
              this.resourses = response.data.data
            }
          } else {
            console.log('faile to get resources')
          }
        }).catch((response) => {
          console.log(555)
        })
      },
      getResources () {
        this.$http.get(window.baseUrl + '/api/resources/list/').then((response) => {
          if (response.data.code === 200) {
            if (this.focus !== '') {
              for (let res of response.data.data) {
                for (let focu of this.focus) {
                  if (focu.id === res.id) {
                    res.add = 0
                    console.log(res.add)
                  }
                }
              }
              this.resourses = response.data.data
            } else {
              this.resourses = response.data.data
            }
          } else {
            console.log('faile to get resources')
          }
        }).catch((response) => {
          console.log(555)
        })
      },
      getNext () {
        if (this.pageNext === null) {
          alert('no next')
        } else {
          this.$http.get(this.pageNext).then((response) => {
            if (response.data.code === 200) {
              this.resourses = response.data.data
              this.pageNext = response.data.next
              this.pagePrevious = response.data.previous
            } else {
              alert(' no next')
            }
          }).catch((response) => {
            alert(' no next')
          })
        }
      },
      getPrevious () {
        if (this.pagePrevious === null) {
          alert('no previous')
        } else {
          this.$http.get(this.pagePrevious).then((response) => {
            if (response.data.code === 200) {
              this.resourses = response.data.data
              this.pageNext = response.data.next
              this.pagePrevious = response.data.previous
            } else {
              alert(' no previous')
            }
          }).catch((response) => {
            alert(' no previous')
          })
        }
      },
      sort () {
        this.$http.post(window.baseUrl + '/api/resourses/sort/').then((response) => {
          if (response.data.code === 200) {
            console.log(123)
          } else {
            console.log(4456)
          }
        }).catch((response) => {
          console.log(12312)
        })
      },
      addCart (resid) {
        this.$http.get(window.baseUrl + '/api/accounts/add_cart/' + resid + '/').then((response) => {
          if (response.data.code === 200) {
            if (response.data.status === 1) {
              alert('已加入购物车')
            } else {
              alert('物品已在购物车')
            }
          } else {
            console.log(4564)
          }
        }).catch((response) => {
          alert('加入购物车失败')
        })
      },
      focusRes (resid) {
        this.$http.post(window.baseUrl + '/api/resources/' + resid + '/focus/').then((response) => {
          if (response.data.code === 200) {
            if (response.data.status === 1) {
              // this.focus = response.data.data
              this.getUserfocus()
            } else {
              this.focus = ''
              alert(response.data.msg)
            }
          } else {
            this.focus = ''
            alert(response.data.msg)
          }
        }).catch((response) => {
          this.focus = ''
          alert(response.data.msg)
        })
      },
      cancleFocusRes (resid) {
        this.$http.post(window.baseUrl + '/api/resources/' + resid + '/cancle_foc/').then((response) => {
          if (response.data.code === 200) {
            if (response.data.status === 1) {
              this.focus = response.data.data
              this.getUserfocus()
            } else {
              this.focus = ''
            }
          } else {
            this.focus = ''
          }
        }).catch((response) => {
          this.focus = ''
        })
      },
      sortRes () {
        this.$http.post(window.baseUrl + '/api/resources/sort/', {
          'sort_way': this.selected
        }).then((response) => {
          if (response.data.code === 200) {
            console.log(this.selected)
            this.resourses = response.data.data
          }
        }).catch((response) => {
          console.log(this.selected)
        })
      },
      getResCategory () {
        this.$http.get(window.baseUrl + '/api/resources/list_category/').then((response) => {
          if (response.data.code === 200) {
            this.categories = response.data.data
          }
        }).catch((response) => {
          console.log(456)
        })
      },
      getResByCate (cateId) {
        this.$http.get(window.baseUrl + '/api/resources/' + cateId + '/get_resource_category/').then((response) => {
          if (response.data.code === 200) {
            this.resourses = response.data.data
          }
        }).catch((response) => {
          console.log(456)
        })
      }
    }
}
</script>



