<template>
    <div id="outermain">
    	<div class="container">
            <div class="row">
            
                <section id="maincontent" class="ten columns positionleft">
                	<div class="padcontent">
                
                    	<section class="content">
                        
                            <div class="breadcrumb"><a href="index.html">Home</a> / 联系我们</div>
                            <h1 class="pagetitle">联系我们</h1>
                            	<div class="frame">
                   				       <img src="./s4.jpg" style="width:100%; height:310px; border:0; margin:0 0 0px 0; display:block;"/>
                              </div>
                                <br>

                            	<h3>联系方式</h3>
                                <div id="contactform">
                                    <form id="contact" action="#">
                                      <fieldset>

                                      <div class="left">
                                      <label>您的称呼：</label>
                                      <input type="text" name="name" id="name" size="50" v-model="name"  class="text-input,inputStyle" style="  border:1px solid black;" />
                                      <label>您的email：</label>
                                      <input type="text" name="email" id="email" size="50" v-model="email"  class="text-input,inputStyle"  style="  border:1px solid black;"/>
                                      	<span class="error" v-show='show' id="name_error">请填写姓名 !</span>
                                      	<span class="error" v-show='show'  id="email_error2">请填写合法的email地址 !</span>
                                      	<span class="error" v-show='show'  id="msg_error">请填写信息!</span>
                                        <p>说出您的意愿，我们会及时与您联系!</p>
                                      </div>
                                      <div class="right">
                                      您想说的话：
                                      <textarea cols="70" rows="13" name="msg" style="  border:1px solid black;" v-model='message' class="text-input"></textarea>
                                      <br class="clear" />
                                      <input type="button"  class="button mini"  @click='sendMessage()' value="联系我们"/>
                                      </div>
                                      </fieldset>
                                    </form>
                                </div><!-- end contactform -->
                           		<p></p>

						            </section>
                     
                    </div>
                </section>
                
                <aside class="two columns">
                
                	<div class="sidebar">
                    <ul>
                        <li class="widget-container">
                            <h2 class="widget-title">我们的地址</h2>
                            <div class="textarea">
                            <p>高新西区百叶路一号<br>
                            电子科技大学成都学院</p>
                            
                            <p>电话: +86 8875156</p>
                            
                            <a href="http://www.cduestc.cn/">http://www.cduestc.cn/</a>
                            </div>
                        </li>
                     
                    </ul>
                    </div>
                    
                </aside>
                
            </div>
        </div>
    </div>
</template>
<style>
  .inputStyle {
    border-color: black;
    border:5px solid red;
  }
</style>

<script>
export default {
  name: 'Contact',
  data () {
    return {
      show: false,
      show_name: false,
      show_emial: false,
      show_message: false,
      name: '',
      email: '',
      message: ''
    }
  },
  methods: {
    sendMessage () {
      this.$http.post(window.baseUrl + '/api/contact/create_contact/', {
        'name': this.name,
        'email': this.email,
        'content': this.message
      }).then((response) => {
        if (response.data.code === 200) {
          alert('留言成功，我们会及时与您联系，谢谢您的参与')
          console.log('ok')
        } else {
          alert('留言失败，请重试')
          console.log('faile')
        }
      }).catch((response) => {
        console.log('555')
      })
    },
    getMess () {
      console.log(this.message)
    }
  }
}
</script>



