<template>
    <div id="outermain">
    	<div class="container">
            <div class="row">
            
                <section id="maincontent" class="ten columns positionleft">
                	<div class="padcontent">
                
                    	<section class="content" id="login">
                        
                            <div class="breadcrumb"><a href="index.html">Home</a> / 注册</div>
                            <h1 class="pagetitle">注册</h1>
                            
                            <div id="login-process">
                                <span class="first">&nbsp;</span>
                                <span class="active">注册</span>
                                <span>登录</span>
                                <span> 选择</span>
                                <span> 领用</span>
                                <span class="last"> 退还</span>
                                <div class="clear"></div>
                            </div>
                            
                            <div class="one_half columns" style="text-align:center">
                                <h3>注册：创建你的账号</h3>
                                <form method="post" action="" id="loginform1">
                                <fieldset>
                                <div style="width:355px">
                                  <label style="margin-left: 100px;">帐号：</label><br />
                                  <div v-if="rusername === false"><input   type="text" id="input" style="border-color:red;" class="regist" v-model='username' /><br /></div>
                                  <div v-else><input type="text"  class="regist" id="input"  v-model= 'username' /><br /></div>
                                </div>


                                <div style="width:355px">
                                  <label style="margin-left: 100px;">居住地址：</label><br />
                                  <div v-if="raddress === false"><input   type="text" id="input" style="border-color:red" class="regist" v-model='address' /><br /></div>
                                  <div v-else><input id="input" type="text" class="regist" v-model='address' /><br /></div>
                                </div>
                                
                                <div style="width:355px">
                                  <label style="margin-left: 100px;">Email：</label><br />
                                  <div v-if="remail === false"><input   type="text" id="input" style="border-color:red" class="regist" v-model='email' /><br /></div>
                                  <div v-else><input id="input"  type="text" class="regist" v-model='email' /><br /></div>
                                </div>


                                <div style="width:355px">
                                  <label style="margin-left: 100px;" >身份证：</label><br />
                                  <div v-if="rid_card === false"><input   type="text"  id="input" style="border-color:red" class="regist" v-model='id_card' /><br /></div>
                                  <div v-else><input  id="input" type="text" class="regist" v-model='id_card' /><br /></div>
                                </div>
                                <div style="margin-left: 152px;">
                                  <label style="margin-left: -65px;">密码：</label><br />
                                  <div v-if="rpassword === false"><input   type="password"  style="border-color:red;width:338px;margin-right:-240px"  id="input1"  v-model='password' /><br /></div>
                                  <div v-else><input style="border-color:black;width:338px;margin-right:-240px"  id="input1" type="password"  v-model='password' /><br /></div>
                                </div>
                                <div style="margin-left: 152px;">
                                <label style="margin-left: -50px;" v-if="communitys !== '' ">所在社区：</label>
                                    <p> 
                                        <select style="border-color:black;width:350px;margin-right:-245px" v-for="community in communitys" v-model='bcommunity'>
                                            <option v-bind:value="community.id"> {{community.name}} </option>
                                        </select>
                                    </p>
                                </div>
                                <div style="folat:right;margin-right:-180px">
                                  <input type="button" class="button" style="margin-left:80px" @click='regist()' value="创建账号" />
                                  <router-link to='/login'><button type="button" class="button" value="已有账号，去登录">已有账号，去登录</button></router-link>
                                </div>
                                </fieldset>
                                </form>

                            </div>
                                     
						          </section>
                     
                    </div>
                </section>
                <!-- <recommended ref="recommended"></recommended> -->

                
            </div>
        </div>
    </div>	
</template>

<style lang="css" scoped>
  .regist {
    margin-right: 300px;
    border-color: black;
    width: 200px;
    border: 1px solid gray
  }
  #input {
    width: 100px;
    margin-left: 200px
  }
  #input1 {
    width: 300px;
    border-color: black;
    border: 1px solid gray;
  }
</style>

<script type="text/javascript">
  import router from '../router'
  import recommended from './sidebars/recommended.vue'
  export default {
    name: 'Login',
    data () {
      return {
        msg: '',
        username: '',
        password: '',
        address: '',
        id_card: '',
        email: '',
        bcommunity: '',
        rusername: true,
        rpassword: true,
        raddress: true,
        rid_card: true,
        remail: true,
        communitys: ''
      }
    },
    components: {
      recommended: recommended
    },
    computed: {
      time () {
        return 1651
      }
    },
    created () {
      this.get_community()
    },
    watch: {
      username: function () {
        if (this.username === '') {
          this.rusername = false
        } else {
          this.rusername = true
        }
      },
      password: function () {
        if (this.password === '') {
          this.rpassword = false
        } else {
          this.rpassword = true
        }
      },
      address: function () {
        if (this.address === '') {
          this.raddress = false
        } else {
          this.raddress = true
        }
      },
      id_card: function () {
        if (this.id_card === '') {
          this.rid_card = false
        } else {
          this.rid_card = true
        }
      },
      email: function () {
        if (this.email === '') {
          this.remail = false
        } else {
          this.remail = true
        }
      }

    },
    methods: {
      showlist () {
        router.push('/login')
        this.show = true
      },
      get_community () {
        this.$http.get(window.baseUrl + '/api/community/list/').then((response) => {
          if (response.results !== []) {
            this.communitys = response.data.results
          } console.log(response.data)
        }).catch((response) => {
          console.log(response.data)
        })
      },
      login () {
        if (this.username === '' || this.password === '') {
          alert('请将信息填写完整')
        } else {
          this.$http.post(window.baseUrl + '/api/accounts/login/', {
            'username': this.username,
            'password': this.password
          }).then((response) => {
            this.un = response.data.data.username
            if (response.data.status === 1) {
              router.push('/')
              router.go(0)
            } else {
              alert(response.data.data.msg)
            }
          }).catch((response) => {
            alert(response.data.data.msg)
          })
        }
      },
      regist () {
        if (this.username === '' || this.password === '' || this.address === '' || this.id_card === '' || this.email === '') {
          alert('请将标红的信息填写完整')
        } else {
          this.$http.post(window.baseUrl + '/api/accounts/regist/' + this.bcommunity + '/',
            {
              'username': this.username,
              'password': this.password,
              'address': this.address,
              'id_card': this.id_card,
              'email': this.email
            }
              ).then((response) => {
                if (response.data.status === 1) {
                  alert(response.data.data.msg)
                  this.login()
                } else {
                  alert(response.data.data.msg)
                }
              }).catch((response) => {
                alert('访问失败')
              })
        }
      }

    }
}
</script>



